@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600;700&display=swap');

:root {
  color-scheme: light
    dark;
  --codely: #9df74d;
  /* 12.75-13.33px */
  --step--1: clamp(
    0.83rem,
    calc(
      0.78rem + 0.07vw
    ),
    0.8rem
  );
  /* 17-20px */
  --step-0: clamp(
    1.06rem,
    calc(
      0.99rem + 0.37vw
    ),
    1.25rem
  );
  /* 22.66-30px */
  --step-1: clamp(
    1.42rem,
    calc(
      1.24rem + 0.9vw
    ),
    1.88rem
  );
  /* 30.21-45px */
  --step-2: clamp(
    1.89rem,
    calc(
      1.53rem + 1.8vw
    ),
    2.81rem
  );
  /* 40.27-67.5px */
  --step-3: clamp(
    2.52rem,
    calc(
      1.85rem + 3.32vw
    ),
    4.22rem
  );
  /* 53.67-101.25px */
  --step-4: clamp(
    3.35rem,
    calc(
      2.19rem + 5.8vw
    ),
    6.33rem
  );
  /* 71.55-151.88px */
  --step-5: clamp(
    4.47rem,
    calc(
      2.51rem + 9.8vw
    ),
    9.49rem
  );
  --aspect-ratio-svg: 1160 /
    6740.2;
}

@font-face {
  font-family: moderat-bold;
  src: url(https://assets.codepen.io/527512/Moderat-Bold.wof);
}

@font-face {
  font-family: moderat-regular;
  src: url(https://assets.codepen.io/527512/Moderat-Regular.woff);
}

.font-xxl {
  font-size: var(
    --step-5
  );
  line-height: 1;
}

.font-xl {
  font-size: var(
    --step-4
  );
}

.font-l {
  font-size: var(
    --step-3
  );
}

.font-m {
  font-size: var(
    --step-2
  );
}

.font-s {
  font-size: var(
    --step-1
  );
}

.font-xs {
  font-size: var(
    --step-0
  );
}

body {
  font-family: 'moderat-bold';
  font-size: 1rem;
  margin: 0;
  background-color: #000000;
  color: #ffffff;
}

.border {
  background-color: var(
    --codely
  );
  height: 60px;
}

#line-svg {
  position: absolute;
  top: 1%;
  left: 0;
  visibility: hidden;
  opacity: 0;
}

section {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#content-wrapper {
  position: relative;
  aspect-ratio: var(
    --aspect-ratio-svg
  );
}

header {
  padding: 1rem 0;
}

.container {
  padding: 2rem 0rem 5rem;
  text-align: center;
  max-width: 1160px;
  margin: auto;
  visibility: hidden;
  opacity: 0;
}

.max-content {
  width: 16ch;
  margin: auto;
}

.logo,
h1,
.start {
  visibility: hidden;
  opacity: 0;
}

.logo {
  width: 100px;
}

h1 {
  padding: 0;
  margin: 0;
}

h1 bold {
  color: #9df74d;
}

.text.start {
  font-family: moderat-regular;
  margin: 0;
}

.text-1 {
  position: absolute;
  top: 8%;
  right: 10%;
  text-align: right;
  margin: 0;
  padding-left: 1rem;
}

.text-2 {
  position: absolute;
  top: calc(
    (0.2 * 33%) + 26%
  );
  right: 10%;
  text-align: right;
}

.text-3 {
  position: absolute;
  top: calc(
    (0.2 * 35%) + 45%
  );
  right: 10%;
  text-align: center;
}

.text-4 {
  position: absolute;
  top: calc(
    (0.2 * 40%) + 56%
  );
  right: 15%;
  text-align: right;
}

.text-5 {
  position: absolute;
  top: calc(
    (0.2 * 50%) + 71%
  );
  text-align: left;
  margin: 0;
  padding-left: 1rem;
}

.text-6 {
  position: absolute;
  top: calc(
    (0.2 * 50%) + 92%
  );
  left: 26%;
  text-align: center;
  margin: 0;
  padding-left: 1rem;
}

@media (min-width: 767px) {
  .container {
    padding: 2rem 0rem
      5rem;
  }
  .max-content {
    width: 24ch;
  }
  .text-1 {
    top: 6%;
    left: 38%;
    text-align: right;
    margin: 0;
    padding-left: 1rem;
  }

  .text-2 {
    top: calc(
      (0.2 * 33%) + 22%
    );
    left: -50%;
  }

  .text-3 {
    top: calc(
      (0.2 * 35%) + 38%
    );
    right: 30%;
    text-align: center;
  }

  .text-4 {
    top: calc(
      (0.2 * 40%) + 56%
    );
    left: 60%;
    text-align: right;
  }

  .text-5 {
    top: calc(
      (0.2 * 50%) + 65%
    );
    left: 60%;
    text-align: left;
    margin: 0;
    padding-left: 1rem;
  }

  .text-6 {
    top: calc(
      (0.2 * 50%) + 82%
    );
    left: 60%;
    text-align: center;
    margin: 0;
    padding-left: 1rem;
  }
}

.social-net {
  display: flex;
  font-size: 1rem;
  align-items: stretch;
  align-content: center;
  flex-wrap: wrap;
  min-height: 620px;
}

.social-net a {
  margin-right: 1rem;
}

.social-net a:hover {
  color: #ffffff;
}
